$font-color-9: #FF7A19;
$font-color-10: #085769;
$font-color-11: #FDB817;
$font-color-12: #89D4DE;

$border-color-1: #2CA6AE;
$border-color-2: #FFB200;
$border-color-3: #3CCCD5;
$border-color-4: #ffc6424d;
$border-color-5: #D79C12;
$border-color-6: #013845;
