:root {
    --slide-movement: 0vw;

    --title-opacity: 1;
    --title-top: 0;
    --title-transition-time: 1s;

    --button-opacity: 1;
    --button-bottom: 0;
    --button-transition-time: 1s;


}

// cookies
#cookieinfo * {
    margin: 0;
    padding: 0;
}

#cookieinfo {
    position: fixed;
    font-size: 12px;
    font-family: Arial, Verdana, sans-serif;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background-color: #323334;
    padding: 20px;
    box-shadow: 0px -1px 3px rgba(0, 0, 0, 0.5);
    z-index: 1000;
    padding-right: 50px;
}

#cookieinfo h6 {
    font-size: 16px;
    font-weight: bold;
    color: #cdd7da;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 1);
    margin-bottom: 7px;
}

#cookieinfo p {
    font-size: 12px;
    color: #afb4bc;
    line-height: 1.5em;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.5);
}

#cookieinfo a {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    line-height: 22px;
    font-size: 16px;
    color: #ffffff;
    text-align: center;
    text-decoration: none;
    background-color: #ff6000;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
}

#cookieinfo a:hover {
    background-color: #ff7925;
}

// *************************

.grecaptcha-badge {
    display: none;
}

.recaptcha-info {
    font-size: 0.8em;
    opacity: 0.5;
    display: flex;
    align-items: center;
    transition: all 0.1s ease-out;
    width: 100%;

    img {
        height: 30px;
        margin-right: 10px;
    }

    &:hover {
        opacity: 1;
    }
}

// *************************

* {
    margin: 0;
    padding: 0;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
    box-sizing: border-box;
    background-color: transparent;
}

html {
    scroll-behavior: smooth;
}

.container {
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
    height: 100%;
}

header {
    position: sticky;
    top: 20px;
    width: calc(100% - 80px);
    margin: 0 40px;
    height: 120px;
    border-radius: 40px;
    z-index: 999;
    background-color: $bg-color-1;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    .header-wrapper {
        // border: solid 1px blue;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100%;

        .logo {
            flex-grow: 1;
            display: flex;
            align-items: center;
            // border: solid 1px red;

            img {
                height: 80px;
                margin: 0 30px;
            }
        }

        .nav-container {
            display: flex;
            justify-content: flex-end;
            flex-grow: 2;
            // border: solid 1px red;
            margin-right: 250px;

            nav {
                display: flex;
                align-items: center;
                // border: solid 1px red;

                ul {
                    display: flex;
                    list-style-type: none;
                    transition: opacity .2s, top .1s;

                    li {
                        display: flex;
                        align-items: center;
                        margin: 2px 0;

                        a {
                            font-family: 'Inria Sans', sans-serif;
                            font-weight: 400;
                            font-size: 20px;
                            color: $font-color-1;
                            padding: 10px 15px;
                            display: flex;
                            align-items: center;

                            &:hover {
                                color: $font-color-4;
                            }

                            &.active {
                                color: $font-color-4;
                            }
                        }

                        .sub-menu {
                            position: absolute;
                            left: -10px;
                            top: -60px;
                            display: flex;
                            flex-direction: column;
                            width: 100%;
                            z-index: 2;
                            min-width: max-content;
                            background-color: $bg-color-1;
                            visibility: hidden;
                            opacity: 0;
                            padding: 10px;
                            box-shadow: 0 2px 4px rgba(0, 0, 0, 10%);
                        }

                        &.parent-sub-menu:hover span {
                            color: $font-color-4;
                        }

                        &.parent-sub-menu {
                            position: relative;

                            a {
                                display: flex;
                                align-items: center;
                                padding: 0px 10px;

                                span {
                                    &.active {
                                        color: $font-color-4;
                                    }
                                }

                                svg {
                                    height: 12px;
                                    margin: 3px 6px 0;
                                    fill: $font-color-1;

                                    &.active {
                                        fill: $font-color-4;
                                    }
                                }
                            }
                        }

                        &.parent-sub-menu:hover .sub-menu {
                            visibility: visible;
                            opacity: 1;
                            top: 45px;
                        }

                        &.parent-sub-menu:hover svg {
                            fill: $font-color-4;
                        }
                    }
                }
            }
        }
    }

    .contact-btn {
        // border: solid 1px blue;
        width: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);

        a {
            display: flex;
            width: 200px;
            height: 50px;
            border: solid 2px $border-color-1;
            justify-content: center;
            align-items: center;
            border-radius: 30px;
            font-family: 'Baloo 2', sans-serif;
            font-weight: 700;
            font-size: 20px;
            color: $font-color-1;
            transition: all 0.1s ease-out;

            &:hover {
                background-color: $bg-color-4;
                color: $font-color-2;
                font-weight: 500;
            }
        }
    }
}

.hero {
    height: 100vh;
    margin-top: -120px;
    width: 100%;
    position: relative;
    overflow: hidden;

    .slider {
        display: flex;
        width: 100%;
        height: 100%;
        position: relative;
        left: 0;

        .slide {
            display: flex;
            width: 100vw;
            height: 100%;
            flex-shrink: 0;
            position: relative;
            left: var(--slide-movement);
            transition: left .7s ease-out;

            img {
                object-fit: cover;
                object-position: top;
                width: 100%;
            }

            .slide-overlay {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                padding-bottom: 25vh;
                align-items: center;
                // border: 1px solid red;

                .titles {
                    margin: 0 20px;
                    margin-top: 180px;
                    width: 70%;
                    // border: 1px solid red;
                    position: relative;

                    top: var(--title-top);
                    opacity: var(--title-opacity);
                    transition: top ease-out, opacity ease-out;
                    transition-duration: var(--title-transition-time);
                    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.9);



                    .sub-title {
                        font-family: 'Baloo 2', sans-serif;
                        font-weight: 600;
                        text-transform: uppercase;
                        color: $font-color-2;
                        font-size: 20px;
                        text-align: center;
                    }

                    .title {
                        font-family: 'Baloo 2', sans-serif;
                        font-weight: 700;
                        color: $font-color-2;
                        font-size: 36px;
                        text-align: center;
                    }
                }

                .buttons {
                    margin-top: 50px;
                    display: flex;
                    gap: 24px;
                    // border: 1px solid red;

                    position: relative;

                    bottom: var(--button-bottom);
                    opacity: var(--button-opacity);
                    transition: bottom, opacity;
                    transition-duration: var(--button-transition-time);

                    .button {
                        width: 200px;
                        height: 50px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 30px;
                        color: $font-color-2;
                        font-family: 'Baloo 2', sans-serif;
                        font-weight: 700;
                        font-size: 20px;

                        &.left {
                            background: $bg-color-2;
                            border: 2px solid $border-color-7;

                            &:hover {
                                background-color: $bg-color-1;
                                color: $font-color-11;
                            }
                        }

                        &.right {
                            background: $bg-color-3;
                            border: 2px solid $border-color-3;

                            &:hover {
                                background-color: $bg-color-1;
                                color: $font-color-7;
                            }
                        }
                    }
                }
            }
        }
    }

    .social {
        position: absolute;
        right: 30px;
        bottom: 30px;
        height: 64px;
        width: 300px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-color: $bg-color-1;
        border-radius: 50px;
        padding: 0 15px;

        img {
            height: 30px;
        }

        a {
            font-family: 'Inria Sans', sans-serif;
            font-weight: 400;
            font-size: 15px;
            color: $font-color-1;
            display: flex;
        }
    }

    .bars {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;

        .bar {
            height: 15px;
            width: 15px;
            border-radius: 50%;
            background-color: $bg-color-1;

            &.active {
                background-color: $bg-color-4;
            }
        }
    }
}

.advantages {

    .cards {
        display: flex;
        flex-wrap: wrap;
        gap: 35px;
        align-items: center;
        justify-content: center;
        margin: 140px 0;

        .card {
            width: 390px;
            height: 260px;
            border-radius: 30px;
            background-color: $bg-color-5;
            padding: 30px;
            transition: all 0.1s ease-out;
            cursor: pointer;

            &:hover {
                background-color: $bg-color-6;
                scale: 105%;
            }

            img {
                height: 60px;
                margin-bottom: 12px;
            }

            .title {
                font-family: 'Baloo 2', sans-serif;
                font-weight: 700;
                color: $font-color-3;
                font-size: 25px;
                text-align: left;
            }

            .content {
                font-family: 'Baloo 2', sans-serif;
                font-weight: 400;
                color: $font-color-3;
                font-size: 20px;
                text-align: left;
            }
        }
    }
}

.video {
    .video-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .title {
            font-family: 'Delicious Handrawn', sans-serif;
            font-weight: 400;
            color: $font-color-3;
            font-size: 90px;
            text-align: center;
            padding: 30px 0 20px;
        }
    }
}

.about {
    background-color: $bg-color-3;

    .title {
        font-family: 'Delicious Handrawn', sans-serif;
        font-weight: 400;
        color: $font-color-2;
        font-size: 90px;
        text-align: center;
        padding: 30px 0 20px;
    }

    .boxes {
        min-height: 100px;
        display: flex;
        flex-wrap: wrap;

        .box {
            min-height: 100px;
            width: 50%;
            display: flex;
            align-items: center;
            margin: 30px 0;
            transition: all 0.2s ease-out;

            .icon {

                img {
                    transition: all 0.1s ease-out;
                    height: 150px;
                }
            }

            .content {
                font-family: 'Baloo 2', sans-serif;
                font-weight: 700;
                color: $font-color-5;
                font-size: 25px;
                list-style-type: none;
                max-width: 300px;
            }

            &.right {
                justify-content: flex-end;

                .icon {
                    order: 2;
                }

                .content {
                    order: 1;
                    margin-right: 35px;
                    text-align: right;
                }

                &.first {
                    padding-right: 170px;
                }

                &.second {
                    padding-right: 250px;
                }

                &.third {
                    padding-right: 170px;
                }
            }

            &.left {
                justify-content: flex-start;

                .icon {
                    order: 1;
                }

                .content {
                    order: 2;
                    margin-left: 35px;
                    text-align: left;
                }

                &.first {
                    padding-left: 170px;
                }

                &.second {
                    padding-left: 250px;
                }

                &.third {
                    padding-left: 170px;
                }
            }

            &:hover img {
                scale: 110%;
                cursor: pointer;
                filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.3));
                scale: 110%;
            }
        }
    }

    .button-wrapper {
        display: flex;
        justify-content: center;

        .button {
            background-color: $bg-color-7;
            width: 200px;
            height: 50px;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: 'Baloo 2', sans-serif;
            font-weight: 500;
            color: $font-color-2;
            font-size: 20px;
            margin-bottom: 40px;
            border: 2px solid $bg-color-7;
            cursor: pointer;

            &:hover {
                background-color: transparent;
                color: $font-color-3;
                font-weight: 700;
            }
        }
    }

    .centre-picture {
        min-height: 100px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.location {

    .location-wrapper {

        min-height: 800px;
        display: flex;

        .map {
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;

            .map-wrapper {
                iframe {
                    height: 500px;
                    width: 600px;
                    left: 0;
                    top: 0;
                    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);
                    border-radius: 80px 80px 80px 0;
                }
            }
        }

        .content-box {
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding-left: 50px;
            padding-bottom: 60px;

            .title {
                font-family: 'Delicious Handrawn', sans-serif;
                font-weight: 400;
                color: $font-color-3;
                font-size: 90px;
                text-align: left;
                margin-bottom: 30px;
            }

            .content {

                p:nth-child(1) {
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $font-color-3;
