.offer-details {
    padding: 80px 0;

    .offer-details-wrapper {
        display: flex;

        .navigation {
            min-height: 500px;
            width: 40%;
            padding: 0 120px;

            .element {
                display: flex;
                align-items: center;
                gap: 24px;
                margin: 65px 0;
                cursor: pointer;
                transition: all 1s ease-out;

                img {
                    height: 136px;
                    transition: all 0.1s ease-out;
                }

                h2 {
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $font-color-3;
                    font-size: 20px;
                    text-align: left;
                    transition: all 0.1s ease-out;
                }

                &:hover h2 {
                    color: $font-color-8;
                    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
                }

                &:hover img {
                    filter: drop-shadow(3px 3px 3px rgba(0, 0, 0, 0.7));
                }
            }
        }

        .content {
            min-height: 500px;
            width: 60%;
            margin: 90px 180px 30px 120px;
            display: flex;
            overflow: hidden;

            .item {
                width: 100%;
                flex-shrink: 0;
                position: relative;
                left: -0%;
                opacity: 100%;
                transition: left .1s ease-out;

                h2 {
