.contact-form-wrapper {
    scroll-margin-top: 170px;
    position: relative;
    opacity: 1;

    .contact-form {
        padding: 0 140px;
        margin: 40px;
        margin-bottom: 80px;
        min-height: 300px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        transition: all 0.3s;

        h1 {
            font-family: 'Baloo 2', sans-serif;
            font-weight: 700;
            color: $font-color-3;
            font-size: 35px;
            text-align: left;
        }

        .form-wrapper {
            margin: 25px 0;
            height: fit-content;
            display: flex;
            width: 100%;
            flex-wrap: wrap;
            gap: 20px 50px;

            .input-wrapper {
                position: relative;
                display: flex;
                background-color: transparent;

                label {
                    position: absolute;
                    display: flex;
                    align-items: center;
                    padding: 0 10px 0 20px;
                    transition: font-size .1s;
                    z-index: 0;
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $font-color-10;
                    font-size: 22px;
                    text-align: left;

                    &.mini {
                        top: 2px;
                        font-size: 14px;
                        opacity: 0.8;
                        align-items: flex-start;
                        z-index: 0;
                        cursor: default;
                        padding-top: 0;
                        color: $font-color-10;
                    }
                }
            }

            .select-wrapper {
                position: relative;
                display: flex;
                background-color: transparent;
                flex-direction: column;
                margin-top: 24px;
                align-items: flex-start;
                width: 100%;
                font-family: 'Baloo 2', sans-serif;

                label {
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $border-color-6;
                    font-size: 20px;
                    text-align: left;
                }

                select {
                    display: none;
                }

                .select-selected {
                    background-color: red;
                }

                /* Style the arrow inside the select element: */
                .select-selected:after {
                    position: absolute;
                    content: "";
                    top: 14px;
                    right: 10px;
                    width: 0;
                    height: 0;
                    border: 6px solid transparent;
                    border-color: #fff transparent transparent transparent;
                }

                /* Point the arrow upwards when the select box is open (active): */
                .select-selected.select-arrow-active:after {
                    border-color: transparent transparent #fff transparent;
                    top: 7px;
                }

                /* style the items (options), including the selected item: */
                .select-items div,
                .select-selected {
                    color: #ffffff;
                    padding: 8px 16px;
                    border: 1px solid transparent;
                    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
                    cursor: pointer;
                }

                /* Style items (options): */
                .select-items {
                    position: absolute;
                    background-color: DodgerBlue;
                    top: 100%;
                    left: 0;
                    right: 0;
                    z-index: 99;
                }

                /* Hide the items when the select box is closed: */
                .select-hide {
                    display: none;
                }

                .select-items div:hover,
                .same-as-selected {
                    background-color: rgba(0, 0, 0, 0.1);
                }
            }

            .textarea-wrapper {
                position: relative;
                display: flex;
                background-color: transparent;
                flex-direction: column;
                margin-top: 24px;
                align-items: flex-start;
                width: 100%;

                label {
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $border-color-6;
                    font-size: 20px;
                    text-align: left;
                }
            }

            .input-wrapper {
                align-items: center;
                width: calc(50% - 25px);

                &.full {
                    width: 100%;
                }
            }

            input[type=text],
            textarea {
                width: 100%;
                padding: 0 20px;
                z-index: 0;
                background-color: transparent;
                padding-top: 8px;
                font-family: 'Baloo 2', sans-serif;
                font-weight: 400;
                color: $font-color-10;
                font-size: 22px;
                text-align: left;

                border: solid 1px $border-color-6;

                &.active {
                    z-index: 1;
                }
            }

            input[type=text] {
                height: 66px;
            }

            textarea {
                height: 135px;
                padding-top: 12px;
                resize: vertical;
            }

            input.error,
            textarea.error {
                border-color: red;
                background-color: rgba(255, 0, 0, .05);
            }

            span.error {
                font-size: 12px;
                color: red;
                padding-left: 15px;

                &.mini {
                    font-size: 14px;
                    align-items: flex-start;
                    z-index: 0;
                    cursor: default;
                    padding-top: 0;
                }
            }

            span#rodo_error {
                // position: absolute;
                width: 100%;
                // left: 140px;
                // top: 3px;
                // flex-shrink: 0;
                padding-left: 30px;
                margin-top: -35px;
            }

            .checkbox-container {
                display: flex;
                align-items: center;
                margin: -10px 0 20px;
                width: 100%;

                .checkbox {
                    display: block;
                    position: relative;
                    height: 20px;
                    width: 20px;
                    cursor: pointer;
                    -webkit-user-select: none;
                    -moz-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
                    flex-shrink: 0;

                    /* Hide the browser's default checkbox */
                    input {
                        position: absolute;
                        opacity: 0;
                        cursor: pointer;
                        height: 100%;
                        width: 100%;
                    }

                    /* Create a custom checkbox */
                    .checkmark {
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 100%;
                        width: 100%;
                        background-color: transparent;
                        border: 1px solid $border-color-6;

                        // border-radius: 8px;
                        &.error {
                            border-color: red;
                            background-color: rgba(255, 0, 0, .05);
                        }
                    }

                    /* On mouse-over, add a background color */
                    &:hover input~.checkmark {}

                    /* When the checkbox is checked, add a background */
                    input:checked~.checkmark {
                        background-color: transparent;
                    }

                    /* When the checkbox is checked, add a background */
                    input:checked:hover~.checkmark {}

                    /* Create the checkmark/indicator (hidden when not checked) */
                    .checkmark:after {
                        content: "";
                        position: absolute;
                        display: none;
                    }

                    /* Show the checkmark when checked */
                    input:checked~.checkmark:after {
                        display: block;
                    }

                    /* Style the checkmark/indicator */
                    .checkmark:after {
                        left: 6px;
                        top: 1px;
                        width: 4px;
                        height: 10px;
                        border: solid $border-color-6;
                        border-width: 0 3px 3px 0;
                        transform: rotate(40deg);
                    }
                }

                .label-text {
                    display: inline-block;
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $font-color-10;
                    font-size: 16px;
                    text-align: left;
                    cursor: pointer;
                    margin: 0 12px;
                }
            }

            .upload-wrapper {
                margin: 25px 0;
                display: flex;
                flex-direction: column;
                border: 1px solid $border-color-6;
                width: 100%;
                padding: 20px;

                input[type=file] {
                    display: flex;
                    flex-direction: row;
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 400;
                    color: $font-color-10;
                    font-size: 18px;
                }

                input::file-selector-button {
                    background-color: transparent;
                    color: $font-color-3;
                    cursor: pointer;
                    border: 1px solid $border-color-6;
                    transition: all .1s ease-in-out;
                    min-width: 180px;
                    text-align: center;
                    border-radius: 15px;
                    width: 150px;
                    height: 32px;
                    margin-right: 20px;
                    font-family: 'Baloo 2', sans-serif;
                    font-weight: 600;
                    color: $font-color-10;
                    font-size: 18px;

