                    display: flex;
                    margin-top: 50px;

                    .icon {
                        height: 17px;
                        width: 17px;
                        border: 1px solid $border-color-6;
                        border-radius: 1px;
                        position: relative;
                        top: 15px;
                        display: flex;
                        transition: rotate 0.5s;
                        margin: 0 10px;

                        &::before {
                            content: "";
                            position: absolute;
                            width: 11px;
                            height: 11px;
                            border-bottom: 1px solid $border-color-6;
                            bottom: 7px;
                            left: 2px;
                        }

                        &::after {
                            content: "";
                            position: absolute;
                            width: 11px;
                            height: 11px;
                            border-left: 1px solid $border-color-6;
                            bottom: 2px;
                            left: 7px;
                            opacity: 1;
                            transition: opacity 0.5s;
