/* LIGHTBOX */

#lightbox {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
    display: none;
    top: 0;
    left: 0;
    // transform: translateY(-50%);

    &.active {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .lightbox-inner {
        position: relative;
        // border: 1px solid blue;
        width: 80vw;
        height: 95vh;
        // overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .img-wrapper {
        position: relative;
        // border: 1px solid red;
        width: 60vw;
        height: 85vh;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;



    }

    .img-wrapper>img {
        // padding: 100px 0 10px 0;
        // object-fit: contain;
        height: 100%;

    }

    .close-btn {
        position: absolute;
        top: 0px;
        right: 0px;
        padding: 10px;
        // border: 1px solid red;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;


        svg {
            width: 80px;
            fill: $font-color-8;
            opacity: 0.5;
        }

        &:hover svg {
            opacity: 1;
        }

    }

    .prev-btn {
        position: absolute;
        top: 50%;
        left: 0cm;
        padding: 30px;
        transform: translateY(-50%);
        cursor: pointer;
        // border: 1px solid red;


        svg {
            width: 80px;
            fill: $font-color-8;
            opacity: 0.5;
        }

        &:hover svg {
            opacity: 1;
        }
    }

    .next-btn {
        position: absolute;
        top: 50%;
        right: 0;
        padding: 30px;
        transform: translateY(-50%);
        // border: 1px solid red;
        cursor: pointer;

        svg {
            width: 80px;
            fill: $font-color-8;
            opacity: 0.5;
        }

        &:hover svg {
            opacity: 1;
        }
    }




    .prev-btn.disabled,
    .next-btn.disabled {
        opacity: 0.1;
    }
}